var curToken
import WxValidate from '../../../utils/WxValidate.js';
import {
	LOGIN,
	SENDCODE
} from "../../../utils/api.js"
export default {
	data() {
		return {
			mobileCode: "获取验证码",
			time: 60,
			mobileTip: "",
			inputValue: "",
			smsTip: "",
			mobile: "",
			smsCode: "",
			isDisable: false,
			bindmobile: ''
		}
	},
	onLoad() {
		this.initValidate();
		this.getUrlToken();
	},
	methods: {
		// 规则校验
		initValidate() {
			// 规则
			const rules = {
				mobile: {
					required: true,
					tel: true
				},
				smsCode: {
					required: true
				}
			}
			// 提示
			const messages = {
				mobile: {
					required: "请输入手机号！"
				},
				smsCode: {
					required: "请输入验证码"
				}
			}
			this.WxValidate = new WxValidate(rules, messages)
		},
		// 获取验证码
		getMobileCode() {
			if (this.bindmobile) {
				if (this.mobileCode == '获取验证码') {
					this.time = 60
					this.timer()
					var jiamiMobile = this.$encruption(this.bindmobile)
					const params = {
						mobile:this.bindmobile,
						elmobi: jiamiMobile
					}
					console.log(params)
					this.$request.url_request(SENDCODE, params, 'POST', res => {
						uni.showToast({
							title: '验证码发送成功',
							icon: 'none',
							duration: 1000
						})
					}, err => {})
				}
			} else {
				uni.showToast({
					title: '手机号不可为空',
					icon: 'none',
					duration: 1000
				})
			}


		},
		timer() {
			if (this.time > 0) {
				this.time--;
				this.mobileCode = "还剩" + this.time + "s"
				setTimeout(this.timer, 1000);
			} else {
				this.time = 0;
				this.mobileCode = "获取验证码";
			}
		},
		// 登录
		handleLogin(e) {
			var that = this
			this.isDisable = true
			setTimeout(function() {
				that.isDisable = false
				that.$forceUpdate()
			}, 1000)
			const params = e.detail.value
			if (!this.WxValidate.checkForm(params)) {
				const error = this.WxValidate.errorList[0]
				// 可以自定义提示
				uni.showToast({
					title: `${error.msg}`,
					icon: 'none'
				});
				return false
			} else {
				var _this = this
				this.$request.url_request(LOGIN, params, "POST", res => {
					if (JSON.parse(res.data).code == 0) {
						uni.navigateTo({
							url: '../../Welcome/Welcome'
						})
					} else {
						if (JSON.parse(res.data).msg) {
							uni.showToast({
								title: JSON.parse(res.data).msg,
								icon: 'none',
								duration: 1000
							})
						}
					}
				}, err => {

				});
			}
		},
		// 截取
		getQueryString(name) {
			var after = window.location.search
			if (after.indexOf('?') === -1) {}
			after = window.location.href.split("?")[1] || after.substr(1);
			if (after) {
				var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
				var r = after.match(reg)
				if (r !== null) {
					return decodeURIComponent(r[2]);
				} else {
					return null
				}
			}
		},
		// 获取当前保存token
		getUrlToken() {
			curToken = this.getQueryString('token')
			if (!curToken) {
				uni.navigateTo({
					url: '../../JumpLogin/JumpLogin'
				})
			} else {
				try {
					uni.setStorageSync('myToken', curToken)
				} catch (e) {
					//TODO handle the exception
					uni.showModal({
						content: e,
						showCancel: false
					});
				}
			}
		},
		// 收起键盘
		pickupKeyboard() {
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 0
			})
		}
	}
}
